@import "./temp1.css";

/*
    NAVBAR STYLING
*/

.navbar {
    width: 100vw;
    position: sticky;

    align-items: center;
    justify-content: space-around;
    background-color: var(--accent-color);
    border: 2px solid var(--secondary-color);
}

.navbar > * {
    margin: 0 3em;
}

.nav-links > ul {
    gap: 2em;
}

/*
    NAVBAR BUTTON STYLING
*/

.nav-link-button {
    width: 3rem;
    height: 3rem;
}

.nav-link-button > a {
    display: flex;
    align-items: center;
    justify-content: center;
}
